.top,
.left,
.bot,
.right,
.topLeft,
.topRight,
.botLeft,
.botRight {
    position: absolute;
    background-color: rgb(28, 28, 28);
    background-image: url('/public/assets/df/borderbg.png');
    width: 8px;
    height: 12px;
}

.topLeft,
.topRight,
.botLeft,
.botRight {
    width: 8px;
    height: 12px;
}

.top,
.bot {
    width: 100%;
    background-repeat: repeat-x;
}

.left,
.right {
    height: 100%;
    background-repeat: repeat-y;
}

.top {
    background-image: url('./images/bordertop.png');
}

.bot {
    background-image: url('./images/borderbot.png');
    background-position-y: 4px;
    bottom: 0;
}

.left {
    background-image: url('./images/borderleft.png');
}

.right {
    background-image: url('./images/borderright.png');
    right: 0;
}

.topLeft {
    top: 0;
    left: 0;
}

.botLeft {
    bottom: 0;
    left: 0;
    background-position: 0 12px;
}

.topRight {
    top: 0;
    right: 0;
    background-position: 8px 0;
}

.botRight {
    bottom: 0;
    right: 0;
    background-position: 8px 12px;
}
