.view {
    display: flex;
    flex-direction: column;
    max-width: 1200px;
    width: 100%;
    align-items: center;
    justify-content: center;
}

.sliders {
    display: flex;
    flex-direction: column;
}

.equipment {
    display: flex;
    flex-wrap: wrap;
}

.equipmentPanel {
    width: 50%;
}

.equipmentPanel:nth-child(2n + 1) {
    padding-right: 20px;
    width: calc(50% - 20px);
}

.equipmentPanel:nth-child(2n) {
    padding-left: 20px;
    width: calc(50% - 20px);
}
