
@font-face {
    font-family: cp437;
    src: url("./cp437.ttf");
}

body {
  margin: 0;
  font-family: cp437, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

input {
    font-family: cp437;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.white {
    color: #FFF;
}

.black {
    color: #000;
}

.grey {
    color: #a1a1a1;
}

.darkgrey {
    color: #4d4d4d;
}

.brightred {
    color: #ff0000;
}

.darkred {
    color: #910000;
}

.orange {
    color: #ff6e00;
}

.brown {
    color: #734a3c;
}

.yellow {
    color: #ffff00;
}

.olive {
    color: #7d7d00;
}

.lime {
    color: #80ff00;
}

.green {
    color: #00821c;
}

.cyan {
    color: #00ffd9;
}

.aquamarine {
    color: #00947e;
}

.lightblue {
    color: #0091ff;
}

.darkblue {
    color: #003d6b;
}

.magenta {
    color: #ff00ee;
}

.purple {
    color: #520091;
}



