.ql-container {
    font-family: inherit !important;
    font-size: inherit !important;
}

.ql-editor {
    padding: 4px 6px;
    border: 1px solid transparent;
    line-height: 1;
}

.render .ql-toolbar.ql-snow {
    display: none;
}

.ql-toolbar.ql-snow .ql-formats {
    margin-right: 0;
}

.render .ql-editor {
    padding: 0;
}

.overlay .ql-editor {
    border: 1px solid #CCC;
}

.overlay .quill .ql-toolbar.ql-snow {
    /* display: block;
    position: relative;
    transform: translateY(0); */
    /* display: block;
    height: auto;
    padding: 8px; */
    display: block;
    width: 45px;
    display: flex;
    justify-content: center;
    border: none;
    /* background: #CCC; */
    padding: 0;
}

.overlay .ql-snow .ql-picker-label {
    padding: 8px 12px;
}

.overlay .ql-snow .ql-color-picker,
.overlay .ql-snow .ql-icon-picker {
    width: 45px;
    height: 38px;
    border: 1px solid #ccc;
}

.ql-container.ql-snow {
    border-color: transparent;
}

.overlay .quill .ql-container.ql-snow {
}

.ql-snow .ql-formats {
    display: none;
}

.overlay .quill .ql-snow .ql-formats {
    display: block;
}
