
.view {
    display: flex;
    flex-direction: column;
    height: 100vh;
    max-width: 1600px;
    margin: 0 auto;
    overflow: hidden;
}

.header {
    padding: .4em 0;
    text-align: center;
    font-size: 2em;
}

.body {
    display: flex;
    flex-grow: 1;
    justify-content: center;
    background-color: #312C34;
    max-height: 100vh;
}

.left {
    flex-basis: 50%;
    padding: 0 2em 3em .6em;
    overflow-y: auto;
}

.right {
    flex-basis: 50%;
    background-color: #312C34;
}

.rightContainer {
    overflow: hidden;
    padding-top: 6px;
    padding-bottom: 6px;
    padding-right: 6px;
    padding-left: 6px;
    margin: 0;
    background-color: #312C34;
}
